.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;

  color: #adaeb9;
}

.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  border-radius: 5px;
  font-weight: 300;
  -webkit-box-shadow: 10px 10px 26px -6px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 26px -6px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 26px -6px rgba(0, 0, 0, 0.75);
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  -webkit-box-shadow: 10px 10px 26px -12px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 26px -12px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 26px -12px rgba(0, 0, 0, 0.75);
}

.movie .movie__title,
.movie .movie__genres {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2f2f2f;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 5px 0;
  display: flex;
}

.movie__year {
  margin: 0;
}
.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}
